<template>
    <active-user
    v-for="user in usersActive"
    :key="user.id"
    :id="user.id"
    :name="user.name"
    :age="user.age"
    @log-out="logOut"
    ></active-user>
    <user-data
    @add-active-user="addActiveUser"
    ></user-data>
</template>

<script>
import ActiveUser from './components/ActiveUser.vue';
import UserData from './components/UserData.vue';

export default {
  components: { 
      ActiveUser,
      UserData
      },
  data() {
      return {
          usersActive: [
              {
                  id: 'john',
                  name: 'John Smit',
                  age: '42',
              },
              {
                  id: 'kate',
                  name: 'Kate Miller',
                  age: '33'
              }
          ],
      }
  },
  methods: {
      logOut(userId) {
          this.usersActive = this.usersActive.filter((user) => user.id !== userId);
      },
      addActiveUser(name, age) {
          const newActiveUser = {
              id: new Date().toISOString(),
              name: name,
              age: age
          };
          this.usersActive.push(newActiveUser);
      }
  }  
}
</script>

<style>
html {
    font-family: sans-serif;
}
section, form {
    margin: 2rem auto;
    max-width: 40rem;
    border-radius: 12px;
    border: 1px solid #ccc;
    padding: 1rem;
}
</style>