<template>
    <form @submit.prevent="submitData">
        <div>
            <input type="text"
            placeholder="your name"
            v-model="enteredName">
        </div>
        <div>
            <input type="text"
            placeholder="your age"
            v-model="enteredAge">
        </div>
        <div>
            <button>Active</button>
        </div>
    </form>
</template>

<script>
export default {
    emits: ['add-active-user'],
    data() {
        return {
            enteredName: '',
            enteredAge: ''
        }
    },
    methods: {
        submitData() {
            this.$emit(
                'add-active-user',
                this.enteredName,
                this.enteredAge
            );
        }
    }
}
</script>